import React, { useState, useEffect } from "react";
import {
  CRow,
  CCol,
  CCard,
  CCardBody,
  CCardHeader,
  CFormGroup,
  CLabel,
  CSpinner,
  CInput,
  CButton,
} from "@coreui/react";
import { useSelector, useDispatch } from "react-redux";
import { useHistory, Redirect } from "react-router-dom";
import {
  SET_CURRENT_USER_EXAM,
  SET_CURRENT_SOAL,
  SET_HISTORY_ID_USER_SOAL,
  SET_HISTORY_STATUS,
  SET_HISTORY_SOAL,
} from "src/redux/actions";
import endpoints from "src/utils/endpoints";
import request from "src/utils/request";
import Header from "../Header";

function DataConfirmation() {
  const history = useHistory();
  const dispatch = useDispatch();
  const { profile } = useSelector((state) => state.user);
  const { totalSoal } = useSelector((state) => state.userSoal);
  const [isLoading, setIsLoading] = useState(false);
  const { current: currentExam } = useSelector((state) => state.dataExam);
  const [formData, setFormData] = useState({
    token: "",
  });
  // ----------------------------------------------------- Methods
  const isLoggedIn = () => (profile.nama ? true : false);

  const chooseExam = async () => {
    setIsLoading(true);
    let res = null;
    try {
      res = await request(null, {
        ...endpoints.chooseExamUser,
        data: {
          id: null,
          user_id: profile.id,
          token: currentExam.token,
        },
      });

      // set logs to question
      if (res.data.status_ujian === "progress") {
        const logs = res.data.exam_user_questions;
        const idSoal = [];
        const dataSoal = [];
        const status = [];
        for (let i = 0; i < logs.length; i++) {
          const el = logs[i];
          // push id_soal
          idSoal.push(el.id);
          // push status & soal
          if (el.exam_user_answer?.jawaban) {
            //push jawaban to status
            status.push({
              status: "dijawab",
              hasil: Boolean(el.exam_user_answer.status_jawaban),
            });

            // push jawaban to soal
            const jawabanUser = el.exam_user_answer.jawaban;
            // let key = "status_jawaban";
            if (
              el.exam_question.exam_question_answer.tipe_jawaban ===
              "isian_singkat"
            ) {
              dataSoal.push({
                ...el.exam_question,
                exam_question_answer: {
                  ...el.exam_question.exam_question_answer,
                  jawaban: el.exam_question.exam_question_answer.jawaban.map(
                    (item, index) => {
                      return {
                        ...item,
                        text_jawaban: jawabanUser[index].text_jawaban,
                      };
                    }
                  ),
                },
              });
            } else {
              dataSoal.push({
                ...el.exam_question,
                exam_question_answer: {
                  ...el.exam_question.exam_question_answer,
                  jawaban: el.exam_question.exam_question_answer.jawaban.map(
                    (item, index) => {
                      return {
                        ...item,
                        status_jawaban: jawabanUser[index].status_jawaban,
                      };
                    }
                  ),
                },
              });
            }
          } else {
            if (
              el.exam_question.exam_question_answer.tipe_jawaban ===
              "isian_singkat"
            ) {
              dataSoal.push({
                ...el.exam_question,
                exam_question_answer: {
                  ...el.exam_question.exam_question_answer,
                  jawaban: el.exam_question.exam_question_answer.jawaban.map(
                    (item) => {
                      return {
                        ...item,
                        text_jawaban: "",
                      };
                    }
                  ),
                },
              });
            } else {
              dataSoal.push({
                ...el.exam_question,
                exam_question_answer: {
                  ...el.exam_question.exam_question_answer,
                  jawaban: el.exam_question.exam_question_answer.jawaban.map(
                    (item) => {
                      return {
                        ...item,
                        status_jawaban: "0",
                      };
                    }
                  ),
                },
              });
            }
            status.push({
              status: "belum_dijawab",
              hasil: false,
            });
          }
        }

        // add status_jawaban
        if (logs.length < totalSoal) {
          for (let i = logs.length; i < totalSoal; i++) {
            status.push({
              status: "belum_dijawab",
              hasil: false,
            });
          }
        }

        dispatch({
          type: SET_HISTORY_ID_USER_SOAL,
          payload: idSoal,
        });

        dispatch({
          type: SET_HISTORY_SOAL,
          payload: dataSoal,
        });

        dispatch({
          type: SET_HISTORY_STATUS,
          payload: status,
        });

        dispatch({
          type: SET_CURRENT_SOAL,
          payload: { index: logs.length - 1 },
        });
      }

      dispatch({ type: SET_CURRENT_USER_EXAM, payload: res.data });
      setIsLoading(false);
      history.push("/exam-start");
    } catch (error) {
      alert(error.error.response.data.message);
      setIsLoading(false);
    }
  };

  useEffect(() => {
    // console.log("reload");
  }, [profile, currentExam]);

  if (!isLoggedIn()) {
    return <Redirect to="/login" />;
  }

  return (
    <Header>
      <CRow className="mt-2">
        <CCol md={7}>
          <CCard className="card-rounded shadow border-white">
            <CCardHeader className="d-flex direction-row justify-content-between align-items-center card-rounded border-0">
              <h6>
                Selamat Datang {profile.nama} dari {profile.school_nama},
                silahkan input token
              </h6>
            </CCardHeader>
            <CCardBody className="pt-0">
              <h4>TOKEN: {currentExam.token}</h4>
            </CCardBody>
          </CCard>
        </CCol>
        <CCol md={5}>
          <CCard className="card-rounded shadow border-white">
            <CCardHeader className="d-flex direction-row justify-content-between align-items-center card-rounded border-0">
              <h3>Konfirmasi Data Peserta</h3>
            </CCardHeader>
            <CCardBody className="pt-0">
              <CFormGroup>
                <CLabel htmlFor="name">NIS</CLabel>
                <CInput
                  readOnly
                  placeholder="Input NIS"
                  onChange={(e) => {}}
                  value={profile.data.user_participant.nis}
                  required
                />
              </CFormGroup>
              <CFormGroup>
                <CLabel htmlFor="name">Nama Peserta</CLabel>
                <CInput
                  readOnly
                  placeholder="Input Nama Peserta"
                  onChange={(e) => {}}
                  value={profile.nama}
                  required
                />
              </CFormGroup>
              <CFormGroup>
                <CLabel htmlFor="name">Jenis Kelamin</CLabel>
                <CInput
                  readOnly
                  placeholder="Input Jenis Kelamin"
                  onChange={(e) => {}}
                  value={profile.data.user_participant.jk}
                  required
                />
              </CFormGroup>
              <CFormGroup>
                <CLabel htmlFor="name">Input Token</CLabel>
                <CInput
                  placeholder="Input Token"
                  onChange={(e) =>
                    setFormData({ ...formData, token: e.target.value })
                  }
                  value={formData.nama}
                  required
                />
              </CFormGroup>
              <CRow>
                <CCol xs={12}>
                  <CButton
                    disabled={currentExam.token !== formData.token || isLoading}
                    block
                    onClick={() => chooseExam()}
                    color="primary"
                  >
                    {isLoading && <CSpinner grow size="sm" />}Submit
                  </CButton>
                </CCol>
              </CRow>
            </CCardBody>
          </CCard>
        </CCol>
      </CRow>
    </Header>
  );
}

export default DataConfirmation;
